import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { getCuentasSocios } from '../../actions/factura';

export const TablaCuentaSocios = () => {

    const dispatch = useDispatch()
    const {cuentaSocios, total} = useSelector(state=> state.factura)

    useEffect(() => {

        let isApiSubscribed =true;
        if(isApiSubscribed){
            dispatch(getCuentasSocios());
        }
         return () => {
            isApiSubscribed = false;
         }
       }, [dispatch])

  return (
    
        <>
        <table className='table table-striped table-hover'>
            <thead>
                <tr>
                    <th>Nombre</th>
                    <th>Monto</th>
                </tr>     
            </thead>
            <tbody>
                {cuentaSocios.map(fac => {
                    return(
                        <tr  key={fac.cedula}>
                            <td>{fac.nombre}</td>
                            <td>{fac.monto}</td>                        
                        </tr>
                        
                    );
                })

                }

            </tbody>
        
        </table>

        <h3>Total: {total}</h3>



    </>



  )
}


    
       

  
